.ng2-c-intro {
  padding: 0 10px;
  text-align: center; }

.ng2-c-intro__title {
  color: #ffffff;
  font-size: 32px;
  margin-bottom: 15px;
  margin-top: 0;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.3); }
  @media (min-width: 768px) {
    .ng2-c-intro__title {
      font-size: 40px; } }

.ng2-c-intro__content {
  color: #ffffff; }
  @media (min-width: 414px) {
    .ng2-c-intro__content {
      font-size: 22px; } }

.ng2-c-intro__help {
  cursor: pointer;
  overflow: visible;
  position: relative;
  color: rgba(255, 255, 255, 0.5);
  font-size: 28px;
  height: 28px; }
  .ng2-c-intro__help::after {
    background: transparent;
    content: '';
    display: block;
    height: 50px;
    left: 50%;
    min-width: 44px;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%); }

.ng2-c-intro__keypass {
  margin: 30px 0 40px; }
  .ng2-c-intro__keypass .icon-keypass {
    color: #ffffff;
    display: inline-block;
    font-size: 60px;
    height: 60px;
    line-height: 47px; }
    .ng2-c-intro__keypass .icon-keypass::before {
      content: "\e802";
      vertical-align: middle; }
    @media (min-width: 768px) {
      .ng2-c-intro__keypass .icon-keypass {
        font-size: 90px;
        height: 90px;
        line-height: 70px; } }

.ng2-c-intro__btn {
  width: 100%; }
  @media (min-width: 414px) {
    .ng2-c-intro__btn {
      width: 300px; } }

@media (min-width: 768px) {
  .ng2-c-logout {
    box-sizing: border-box;
    width: 100%; } }

.ng2-c-logout__title {
  text-align: center; }

.ng2-c-logout__countdown {
  color: #28648a;
  display: block;
  font-size: 72px;
  margin: 20px 0;
  text-align: center; }

.e-placeholder--cash {
  height: 58px; }

.e-placeholder--avatar-large {
  height: 100px; }

.e-placeholder--h-250 {
  min-height: 250px; }

.e-placeholder--market-section {
  min-height: 550px; }

.e-placeholder--contract-selection {
  min-height: 98px; }
  @media (min-width: 768px) {
    .e-placeholder--contract-selection {
      min-height: 101px; } }

.e-portfolio-transfers {
  width: 85%; }
  @media (min-width: 768px) {
    .e-portfolio-transfers {
      max-width: 85%;
      width: auto; } }

.e-portfolio-transfers__amount {
  margin-left: 35px;
  width: 100%; }
  @media (min-width: 414px) {
    .e-portfolio-transfers__amount {
      margin-left: 45px; } }
  @media (min-width: 768px) {
    .e-portfolio-transfers__amount {
      flex-grow: 1;
      margin-left: 20px;
      text-align: right;
      width: auto; } }
